.row
{
    padding: 20px;
    width: 100%;
    box-sizing: border-box;
    
}
.row::-webkit-scrollbar
{
    display: none;
}
.movie-lists
{
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    margin: 0px 0px;
}
.movie-lists::-webkit-scrollbar
{
    display: none;
}
.movie-lists img
{
    height: 150px;
    margin-right: 20px;
    padding: 20px 0px;
    transition: 0.2s ease all;
    cursor: pointer;
}
.movie-lists img:hover
{
    transform: scale(1.08);
}
.row_poster_large
{
    height: 300px !important;
}