header
{
    width: 100%;
    height: 500px;
    margin: 0px;
    padding: 0px;
    position: relative;
}
nav
{
    height: 70px;
    box-sizing: border-box;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0px;
    width: 100% !important;
    z-index: 11111;
    transition: 0.5s ease all;
}
.nav-active
{
    background-color: rgba(0, 0, 0, 0.5);
}
.banner-img,
.logo-img
{
    height: 100%;
}
.banner-contents
{
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
}
.banner_btns
{
    margin: 20px 0px;
    width: 170px;
    display: flex;
    justify-content: space-between;

}
.banner_btns button
{
    padding: 10px 20px;
    background-color: rgba(0, 0, 0, 0.5);
    border-style: none;
    color: #fff;
    cursor: pointer;
    transition: 0.2s ease all;

}
.banner_btns button:hover
{
    background-color: rgba(0, 0, 0, 0.8);
}
.banner_desc
{
    width: 700px;
    font-size: 20px;
}
.fade_section
{
    position: absolute;
    bottom: 0px;
    width: 100%;
    height: 100px;
    background-image: linear-gradient(180deg, transparent, rgba(37,37,37,0.61), #000);
}


@media screen and (max-width:768px){
    .banner_desc
{
    width: 100%;
    font-size: 17px;
}
}